"use client";

import { motion } from "framer-motion";
import Link from "next/link";
import { useState } from "react";
import { Counter } from "@/components/counter";
import { StickyCallButton } from "@/components/sticky-cta";
import { TrustBadges } from "@/components/trust-badges";
import { InteractiveQuiz } from "@/components/interactive-quiz";
import { useQuiz } from "@/context/quiz-context";

export default function Home() {
  const [activeTab, setActiveTab] = useState(0);
  const { isQuizOpen, closeQuiz, openQuiz } = useQuiz();

  return (
    <main className="relative bg-white text-black overflow-hidden font-sans">
      <StickyCallButton />
      <InteractiveQuiz isOpen={isQuizOpen} onClose={closeQuiz} />

      {/* ================= HERO ================= */}
      <section className="relative min-h-[90vh] flex items-center justify-center overflow-hidden bg-white">
        <div className="absolute inset-0 z-0 text-slate-50">
          <div className="absolute top-0 right-0 w-[600px] h-[600px] bg-current rounded-full blur-[120px] opacity-40 translate-x-1/3 -translate-y-1/3" />
          <div className="absolute bottom-0 left-0 w-[600px] h-[600px] bg-current rounded-full blur-[120px] opacity-40 -translate-x-1/3 translate-y-1/3" />
        </div>

        <div className="relative z-20 max-w-7xl mx-auto px-6 text-center pt-20">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5 }}
            className="inline-block mb-10"
          >
            <span className="px-6 py-2 bg-slate-100 border border-slate-200 rounded-full text-gray-500 text-xs font-black uppercase tracking-[0.2em]">
              The AI Implementation Consultancy
            </span>
          </motion.div>

          <motion.h1
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.1 }}
            className="text-5xl md:text-8xl lg:text-9xl font-black text-black mb-10 leading-[0.95] tracking-tighter"
          >
            Stop Wondering About AI.
            <span className="block text-gray-300 mt-4">Start Solving Problems.</span>
          </motion.h1>

          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2, duration: 0.6 }}
            className="text-xl md:text-3xl text-gray-600 max-w-5xl mx-auto mb-16 leading-tight font-medium"
          >
            We design and implement targeted AI agent workflows that create new capabilities and deliver a measurable return on investment for SMBs.
          </motion.p>

          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3, duration: 0.6 }}
            className="flex flex-col sm:flex-row gap-6 justify-center items-center mb-16"
          >
            <button
              onClick={openQuiz}
              className="group px-12 py-6 bg-black text-white font-black text-xl rounded-full shadow-2xl hover:shadow-black/20 hover:scale-[1.02] transition-all duration-300 flex items-center gap-3"
            >
              Get Your AI Roadmap
              <svg className="w-6 h-6 group-hover:translate-x-1 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={3}>
                <path strokeLinecap="round" strokeLinejoin="round" d="M17 8l4 4m0 0l-4 4m4-4H3" />
              </svg>
            </button>
          </motion.div>

          {/* Trust Indicators */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.5 }}
            className="flex flex-wrap items-center justify-center gap-12 text-[10px] md:text-xs text-gray-400 font-black uppercase tracking-[0.2em]"
          >
            <div className="flex items-center gap-2">
              <span className="w-2 h-2 bg-black rounded-full" />
              <span>No long-term contracts</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="w-2 h-2 bg-black rounded-full" />
              <span>Results in 2-4 weeks</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="w-2 h-2 bg-black rounded-full" />
              <span>Your data stays private</span>
            </div>
          </motion.div>
        </div>
      </section>

      {/* ================= THE $1K ROADMAP ================= */}
      <section className="relative py-32 bg-slate-50 border-y border-slate-200">
        <div className="max-w-6xl mx-auto px-6">
          <div className="grid lg:grid-cols-2 gap-20 items-center">
            <div>
              <span className="text-xs font-black uppercase tracking-[0.2em] text-gray-400 mb-6 block">Immediate Value</span>
              <h2 className="text-4xl md:text-6xl font-black text-black mb-8 tracking-tighter uppercase">The $1k AI Roadmap</h2>
              <p className="text-xl md:text-2xl text-gray-600 mb-10 leading-relaxed font-medium">
                A low-risk, fixed-price discovery package to de-risk your investment and identify your highest-impact automation opportunity.
              </p>
              <div className="space-y-6 mb-12">
                {[
                  "1 week intensive discovery session",
                  "Identify target automation workflows",
                  "Complete strategic implementation plan",
                  "Simple Proof-of-Concept demonstration"
                ].map((feature, i) => (
                  <div key={i} className="flex items-center gap-4 text-gray-700 font-bold">
                    <div className="w-5 h-5 flex-shrink-0 bg-black text-white rounded-full flex items-center justify-center">
                      <svg className="w-3 h-3" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={4}>
                        <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
                      </svg>
                    </div>
                    {feature}
                  </div>
                ))}
              </div>
              <button
                onClick={openQuiz}
                className="px-10 py-5 bg-black text-white font-black uppercase tracking-widest text-sm rounded-full hover:bg-gray-800 transition-all shadow-xl shadow-black/10"
              >
                Secure Your Roadmap
              </button>
            </div>
            <div className="relative">
              <div className="bg-white p-12 rounded-[40px] border border-slate-200 shadow-notion relative z-10">
                <div className="flex items-center gap-6 mb-10">
                  <div className="w-16 h-16 bg-black rounded-2xl flex items-center justify-center text-white text-3xl font-black">1</div>
                  <div>
                    <h4 className="font-black text-black uppercase tracking-tight text-xl">Seven Days</h4>
                    <p className="text-sm text-gray-400 font-bold uppercase tracking-widest">Discovery to Blueprint</p>
                  </div>
                </div>
                <div className="space-y-8">
                  <div className="h-4 w-3/4 bg-slate-50 rounded-full" />
                  <div className="h-4 w-1/2 bg-slate-50 rounded-full" />
                  <div className="h-4 w-5/6 bg-slate-50 rounded-full" />
                  <div className="pt-10 border-t border-slate-100 mt-10">
                    <p className="text-xs font-black text-black uppercase tracking-[0.2em] mb-4">Final Deliverable:</p>
                    <p className="text-xl text-gray-500 font-bold italic tracking-tight leading-relaxed">"The implementation bridge between your current data and autonomous execution."</p>
                  </div>
                </div>
              </div>
              <div className="absolute top-8 left-8 w-full h-full bg-slate-200 rounded-[40px] -z-10" />
            </div>
          </div>
        </div>
      </section>

      {/* ================= PROBLEM SECTION ================= */}
      <section className="relative py-32 bg-white">
        <div className="max-w-6xl mx-auto px-6">
          <div className="text-center mb-24">
            <span className="text-xs font-black uppercase tracking-[0.3em] text-gray-400 mb-6 block">The Implementation Gap</span>
            <h2 className="text-4xl md:text-6xl font-black text-black mb-6 tracking-tight">
              Does this <span className="text-gray-300">sound</span> familiar?
            </h2>
          </div>

          <div className="grid md:grid-cols-2 gap-10">
            {[
              {
                problem: "Your team spends hours on copy-paste work",
                reality: "Data entry, email responses, schedulingâ€”tasks that feel like a waste of talent.",
                icon: "â°"
              },
              {
                problem: "You're drowning in customer requests",
                reality: "Support tickets pile up. Response times are slow. Customers are frustrated.",
                icon: "ðŸ“§"
              },
              {
                problem: "You can't afford to hire more people",
                reality: "Salaries, benefits, trainingâ€”it's expensive. But the work still needs to get done.",
                icon: "ðŸ’°"
              },
              {
                problem: "You've tried automationâ€”it didn't work",
                reality: "Too rigid. Broke on edge cases. Required constant maintenance. Not worth it.",
                icon: "ðŸ”§"
              },
            ].map((item, i) => (
              <div key={i} className="p-10 border border-slate-100 rounded-3xl hover:border-black transition-all duration-300 bg-slate-50/50">
                <div className="text-5xl mb-6">{item.icon}</div>
                <h3 className="text-2xl font-black mb-4 tracking-tight">{item.problem}</h3>
                <p className="text-gray-600 font-medium leading-relaxed">{item.reality}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* ================= RESULTS / COUNTERS ================= */}
      <section className="relative py-32 bg-black text-white">
        <div className="max-w-7xl mx-auto px-6">
          <div className="grid md:grid-cols-3 gap-20">
            <div className="text-center">
              <div className="text-7xl md:text-8xl font-black mb-4 tracking-tighter">
                <Counter end={10} suffix="+" />
              </div>
              <p className="text-gray-400 font-black uppercase tracking-widest text-xs">Hours saved per week</p>
            </div>
            <div className="text-center">
              <div className="text-7xl md:text-8xl font-black mb-4 tracking-tighter">
                2-4
              </div>
              <p className="text-gray-400 font-black uppercase tracking-widest text-xs">Weeks to launch</p>
            </div>
            <div className="text-center">
              <div className="text-7xl md:text-8xl font-black mb-4 tracking-tighter">
                RO<span className="text-gray-600 italic">I</span>
              </div>
              <p className="text-gray-400 font-black uppercase tracking-widest text-xs">Measurable impact</p>
            </div>
          </div>
        </div>
      </section>

      {/* ================= TRUST BADGES ================= */}
      <section className="relative py-24 bg-white border-b border-slate-100">
        <div className="max-w-6xl mx-auto px-6">
          <TrustBadges />
        </div>
      </section>

      {/* ================= FINAL CTA ================= */}
      <section className="relative py-32 bg-white text-center">
        <div className="max-w-4xl mx-auto px-6">
          <h2 className="text-4xl md:text-8xl font-black text-black mb-10 tracking-tighter">Start your transition.</h2>
          <p className="text-xl md:text-2xl text-gray-600 mb-16 font-medium max-w-2xl mx-auto">Join the ambitious SMBs turning AI into their competitive advantage today.</p>
          <button
            onClick={openQuiz}
            className="px-12 py-6 bg-black text-white font-black uppercase tracking-widest text-sm rounded-full hover:bg-gray-800 transition-all shadow-notion"
          >
            Get My Custom Roadmap
          </button>
        </div>
      </section>
    </main>
  );
}
